import { UmbControllerHostElement } from "@umbraco-cms/backoffice/controller-api";
import { UmbDefaultWorkspaceContext } from "@umbraco-cms/backoffice/workspace";
import { SEOTOOLKIT_MODULE_ENTITY } from "../constants/seoToolkitConstants";

export class SeoToolkitModuleContext extends UmbDefaultWorkspaceContext {
    constructor(host: UmbControllerHostElement) {
        super(host);
        //this.provideContext(UMB_WORKSPACE_CONTEXT, this);
        this.provideContext("seoToolkit.modules.context", this);
    }

    getEntityType(): string {
        return SEOTOOLKIT_MODULE_ENTITY;
    }

    getUnique(): string | undefined {
        return undefined;
    }
}